import React, { useRef, useEffect, useState } from "react";
import { Link, useStaticQuery, graphql } from "gatsby";
import animations from "../../lib/animations.lib";
import logo from "../../images/logo.svg";
import "./navbar.styles.scss";

const animate = animations();

const Navbar = () => {
  const [isMobile, setIsMobile] = useState(false);

  const { site } = useStaticQuery(query);
  const links = site.siteMetadata?.navbar.links;
  const navRef = useRef(null);

  const handleWindowSizeChange = () => {
    if (window.innerWidth < 1024) {
      setIsMobile(true);
    }
  };

  useEffect(() => {
    handleWindowSizeChange();

    window.addEventListener("resize", handleWindowSizeChange);
    return () => {
      window.removeEventListener("resize", handleWindowSizeChange);
    };
  }, []);

  useEffect(() => {
    if (isMobile) {
      // setDeviceType("Mobile");
      console.log("mobile");
      animate.toggleNavbar(navRef.current);
      animate.animateNavbar(navRef.current, false);
    } else {
      // setDeviceType("Desktop");
      console.log("desktop");
      animate.animateNavbar(navRef.current);
    }
  }, [isMobile]);

  return (
    <nav
      ref={navRef}
      className="navbar is-transparent is-uppercase is-fixed-top"
      aria-label="main navigation"
    >
      <div className="container">
        <div className="navbar-brand">
          <Link className="navbar-item p-3" to="/">
            <img
              className="image"
              src={`${logo}`}
              alt="logo"
              style={{ width: "auto", height: "8vh" }}
            />
            {/* {site.siteMetadata?.title || `La Fucina dei Talenti`} */}
          </Link>
          <button
            className="navbar-burger"
            aria-label="menu"
            aria-expanded="false"
            data-target="navbar"
          >
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </button>
        </div>

        <div id="navbar" className="navbar-menu has-text-right-mobile">
          <div className="navbar-end">
            {links &&
              links.map((link, i) => (
                <Link
                  key={i}
                  to={link.to}
                  className="navbar-item has-text-capitalized"
                >
                  {link.name}
                </Link>
              ))}
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;

const query = graphql`
  query NavbarData {
    site {
      siteMetadata {
        title
        navbar {
          links {
            name
            to
          }
        }
      }
    }
  }
`;
