import React, { useState, useEffect, useContext } from "react";
import Navbar from "../components/navbar/navbar.component";
import Footer from "../components/footer/footer.component";
import { CookiesContext } from "../contexts/cookies.context";
import { Link } from "gatsby";
import { useLocation } from "@gatsbyjs/reach-router"; // this helps tracking the location
import { initializeAndTrack } from "gatsby-plugin-gdpr-cookies";
import CookieConsent, { getCookieConsentValue } from "react-cookie-consent";

import "../styles/lfdt.scss";

const Layout = ({ pageClass, children }) => {
  const location = useLocation();
  const { cookiesStatus, setCookiesStatus } = useContext(CookiesContext);
  const [visibilityStatus, setVisibilityStatus] = useState("byCookieValue");

  const getVisibilityStatus = (cookiesStatus) => {
    if (cookiesStatus === undefined) {
      return "show";
    } else {
      return "hidden";
    }
  };

  useEffect(() => {
    setVisibilityStatus(getVisibilityStatus(cookiesStatus));
  }, [cookiesStatus]);

  return (
    <>
      <Navbar />
      <main
        className={`${pageClass}`}
        style={{
          minHeight: "80vh",
          marginBottom: "5rem",
        }}
      >
        {children}
      </main>
      <Footer />
      <CookieConsent
        visible={visibilityStatus}
        location="bottom"
        buttonText=" Accetto"
        onAccept={() => {
          setCookiesStatus(
            getCookieConsentValue("gatsby-gdpr-google-analytics")
          );
          initializeAndTrack(location);
        }}
        enableDeclineButton={true}
        declineButtonText="Rifiuto"
        onDecline={() => {
          setCookiesStatus(
            getCookieConsentValue("gatsby-gdpr-google-analytics")
          );
        }}
        cookieName="gatsby-gdpr-google-analytics"
        expires={150}
        containerClasses="p-3"
        buttonClasses="button is-primary mx-3"
        declineButtonClasses="button is-danger mx-3"
        style={{
          background: "#dfeffe",
          color: "#333333",
          alignItems: "center",
        }}
        disableButtonStyles={true}
      >
        Questo sito utilizza cookies di terze parti per migliorare la tua
        esperienza di navigazione.{" "}
        <Link to="/privacy-cookies" style={{ marginLeft: "0.2rem" }}>
          Approfondisci
        </Link>
      </CookieConsent>
    </>
  );
};

export default Layout;
