import React from "react";
import { Link, useStaticQuery, graphql } from "gatsby";

import {
  FaFacebookSquare,
  FaInstagramSquare,
  FaYoutubeSquare,
  FaLinkedin,
} from "react-icons/fa";

import CookiesStatus from "../../components/cookies-status/cookies-status.component";

import "./footer.styles.scss";

const Footer = () => {
  const { site } = useStaticQuery(query);

  return (
    <footer className="footer">
      <section className="section footer-links">
        <div className="container">
          <div className="columns is-mobile is-multiline">
            <div className="column is-half-mobile has-text-centered">
              <ul>
                <li>
                  <a
                    href={site.siteMetadata.socials.facebook}
                    target="_blank"
                    rel="noreferrer"
                  >
                    <FaFacebookSquare /> Facebook
                  </a>
                </li>
                <li>
                  <a
                    href={site.siteMetadata.socials.instagram}
                    target="_blank"
                    rel="noreferrer"
                  >
                    <FaInstagramSquare /> Instagram
                  </a>
                </li>
              </ul>
            </div>
            <div className="column is-half-mobile has-text-centered">
              <ul>
                <li>
                  <a
                    href={site.siteMetadata.socials.youtube}
                    target="_blank"
                    rel="noreferrer"
                  >
                    <FaYoutubeSquare /> YouTube
                  </a>
                </li>
                <li>
                  <a
                    href={site.siteMetadata.socials.linkedin}
                    target="_blank"
                    rel="noreferrer"
                  >
                    <FaLinkedin /> Linkedin
                  </a>
                </li>
              </ul>
            </div>
            <div className="column has-text-centered">
              <p>
                <Link to="/privacy-cookies">Privacy e Cookies policy</Link>
              </p>
              <CookiesStatus />
            </div>
          </div>
        </div>
      </section>
      <section className="section">
        <div className="container">
          <div className="columns">
            <div className="column has-text-centered">
              <p>
                &#169;{new Date().getFullYear()},{" "}
                {site.siteMetadata?.title || `La Fucina dei Talenti`} Srls |
                Codice fiscale e n.iscr. al Registro Imprese:{" "}
                {site.siteMetadata?.cf}
              </p>
            </div>
          </div>
        </div>
      </section>
    </footer>
  );
};

export default Footer;

const query = graphql`
  query FooterData {
    site {
      siteMetadata {
        title
        cf
        socials {
          instagram
          facebook
          youtube
          linkedin
        }
      }
    }
  }
`;
