import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

const closeNavbar = (el) => {
  const burger = el.querySelector(".navbar-burger");

  if (burger) {
    const targetId = burger.dataset.target;
    const $target = document.getElementById(targetId);

    el.classList.contains("is-active") && el.classList.remove("is-active");
    $target.classList.contains("is-active") &&
      $target.classList.remove("is-active");
  }
};

const animations = () => {
  const minimizeHeader = (el, onHover = true) => {
    const showAnim = gsap
      .from(el, {
        yPercent: -100,
        paused: true,
        duration: 0.4,
        onReverseComplete: () => {
          closeNavbar(el);
        },
      })
      .progress(1);

    ScrollTrigger.create({
      start: "top top",
      end: 99999,
      onUpdate: (self) => {
        if (self.direction === -1) {
          showAnim.play();
        } else {
          showAnim.reverse();
        }
      },
    });

    // hide after 2s
    window.setTimeout(function () {
      showAnim.reverse();
    }, 2000);

    if (onHover) {
      window.addEventListener("mousemove", (e) => {
        if (e.y < 300) {
          showAnim.play();
        }
      });
    }
  };

  const fadeInAnimation = () => {
    const boxes = gsap.utils.toArray(".fade-in");

    boxes.forEach((box) => {
      const image = box.querySelectorAll(".gatsby-image-wrapper");
      // var fig = $(box).find('figure');
      gsap.from(box, {
        autoAlpha: 0,
        yPercent: 25,
        scrollTrigger: {
          trigger: box,
          start: "top 80%",
          // immediateRender: false,
          // markers: true
        },
        duration: 1.2,
      });

      gsap.from(image, {
        autoAlpha: 0,
        yPercent: 25,
        delay: 0.5,
        scrollTrigger: {
          trigger: box,
          start: "top 80%",
          // immediateRender: false,
          // markers: true
        },
        duration: 0.8,
      });
    });
  };

  gsap.registerPlugin(ScrollTrigger);
  const master = gsap.timeline();

  const animateNavbar = (el, onHover = true) => {
    master.add(minimizeHeader(el, onHover));
  };

  const animateFadeIn = () => {
    master.add(fadeInAnimation, ">");
  };

  const toggleNavbar = (el) => {
    const burger = el.querySelector(".navbar-burger");

    if (burger) {
      burger.addEventListener("click", () => {
        const targetId = burger.dataset.target;
        const $target = document.getElementById(targetId);

        el.classList.toggle("is-active");
        $target.classList.toggle("is-active");
      });
    }
  };

  return {
    animateNavbar,
    animateFadeIn,
    toggleNavbar,
  };
};

export default animations;
