import React, { useContext } from "react";
import Cookies from "js-cookie";
import { CookiesContext } from "../../contexts/cookies.context";
import { FaThumbsUp, FaThumbsDown } from "react-icons/fa";
import { resetCookieConsentValue } from "react-cookie-consent";

const deleteAllGACookies = () => {
  const neededAttributes = {
    path: "/",
    domain: ".fucinatalenti.it",
  };
  Object.keys(Cookies.get())
    .filter((key) => !key.includes("gatsby-gdpr"))
    .forEach((cookieName) => {
      Cookies.remove(cookieName, neededAttributes);
    });
};

// import './cookies-status.styles.scss';
const CookiesStatus = () => {
  const { cookiesStatus, setCookiesStatus } = useContext(CookiesContext);

  const handleClick = (e) => {
    e.preventDefault();
    deleteAllGACookies();
    setCookiesStatus(resetCookieConsentValue("gatsby-gdpr-google-analytics"));
  };

  return (
    <div className="mt-3">
      <p className="mb-1">
        <span className="has-text-weight-bold">Scelta cookies</span>:
        {cookiesStatus === undefined ? (
          <span className="ml-2">Nessuna preferenza</span>
        ) : cookiesStatus === "true" ? (
          <span className="ml-2">
            <FaThumbsUp className="pt-1 mb-1 mr-1" />
            Accettati
          </span>
        ) : (
          <span className="ml-2">
            <FaThumbsDown className="pt-1 mr-1" />
            Rifiutati
          </span>
        )}
      </p>
      <button className="button is-ghost" onClick={handleClick}>
        Cancella preferenze cookies
      </button>
    </div>
  );
};

export default CookiesStatus;
